<template>
  <v-form>
    <v-container fluid>
      <v-row>
        <v-col cols="12">
          <v-text-field
            v-model="title"
            label="Заголовок"
            outlined
            required
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <v-textarea
            v-model="description"
            label="Опис"
            outlined
            color="teal"
            required
          >
          </v-textarea>
        </v-col>
        <v-row>
          <v-col cols="1">
            <v-text-field
              class="ml-3"
              v-model="price"
              type="number"
              label="Ціна"
            ></v-text-field>
          </v-col>
          <v-col cols="11">
            <v-file-input
              class="mr-3"
              v-model="files"
              prepend-icon="mdi-paperclip"
              show-size
              multiple
            >
              <template v-slot:selection="{ text }">
                <v-chip small label color="primary">
                  {{ text }}
                </v-chip>
              </template>
            </v-file-input></v-col
          >
        </v-row>
        <ApolloMutation
          :mutation="$options.mutations.createPostMutation"
          :variables="{
            post: {
              category_id: 1,
              title: this.title,
              description: this.description,
              price: getPrice,
              picture_url: 'abc',
              is_active: true,
              type: 'REQ',
            },
          }"
          v-slot="{ mutate }"
          @done="notify('complete')"
        >
          <v-col cols="12"> <v-btn @click="mutate()">Створити</v-btn> </v-col>
        </ApolloMutation>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { ApolloMutation } from 'vue-apollo'
import createPostMutation from '@/graphql/mutations/createPost.gql'
export default {
  components: {
    ApolloMutation
  },
  mutations: {
    createPostMutation
  },
  data: () => ({
    title: '',
    description: '',
    price: 0,
    files: []
  }),
  methods: {
    notify (...args) {
      window.console.log('notify:', ...args)
    }
  },
  computed: {
    getPrice: function () {
      const result = parseInt(this.price, 10)
      return result
    }
  }
}
</script>

